"use client";

import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  Legend,
} from "recharts";

import { useState, useEffect, useMemo } from "react";
import { useSearchParams } from "next/navigation";
import { supabase } from "../../../lib/supabaseClient";
import { useCashflowData } from "../dashboard/components/useCashflowData";
import {
  categorizeExpense
} from "../dashboard/components/ExpensePieChart"; // â† SAME FILE

const expenseByCategory = {};

transactions.forEach((t) => {
  if (!t.withdrawal || t.withdrawal <= 0) return;

  const category = categorizeExpense(t.description || "");

  expenseByCategory[category] =
    (expenseByCategory[category] || 0) + Number(t.withdrawal);
});

const chartData = Object.entries(expenseByCategory).map(
  ([category, spent]) => ({
    category,
    spent,
    allocated:
      allocations.find(a => a.category === category)?.allocated || 0,
  })
);

export default function ExpensesPage() {
  const searchParams = useSearchParams();

  // =========================
  // YEAR
  // =========================
  const START_YEAR = 2025;
  const currentYear = new Date().getFullYear();
  const yearFromUrl = Number(searchParams.get("year"));

  const selectedYear =
    yearFromUrl && yearFromUrl >= START_YEAR
      ? yearFromUrl
      : currentYear;

  // =========================
  // EXPENSE DATA
  // =========================
  const { transactions: fetchedTransactions } =
    useCashflowData(selectedYear);

  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    setTransactions(fetchedTransactions || []);
  }, [fetchedTransactions]);

  // =========================
  // ALLOCATION TOTALS (FROM budget_uploads)
  // =========================
  const [allocations, setAllocations] = useState([]);

useEffect(() => {
  async function fetchAllocations() {
    const { data, error } = await supabase
      .from("budget_uploads")
      .select("id, category, amount, description")
      .eq("year", selectedYear);

    if (error) {
      console.error("Allocation fetch error:", error);
      return;
    }

    // âœ… KEEP ONLY TOTAL ROWS
    const totals = (data || []).filter(
      (row) =>
        row.description &&
        row.description.trim().toLowerCase() === "total"
    );

    setAllocations(
      totals.map((row) => ({
        id: row.id,                // âœ… UNIQUE KEY
        category: normalizeCategory(row.category),
        allocated: Number(row.amount),
      }))
    );
  }

  fetchAllocations();
}, [selectedYear]);

const [expandedCategory, setExpandedCategory] = useState(null);

function toggleCategory(category) {
  setExpandedCategory((prev) =>
    prev === category ? null : category
  );
}

const [subcategories, setSubcategories] = useState({});


async function fetchSubcategories(category) {
  const { data, error } = await supabase
    .from("budget_uploads")
    .select("id, description, amount")
    .eq("year", selectedYear)
    .eq("category", category)
    .neq("description", "Total");

  if (error) {
    console.error("Subcategory fetch error:", error);
    return;
  }

  setSubcategories((prev) => ({
    ...prev,
    [category]: data || [],
  }));
}

function toggleCategory(category) {
  setExpandedCategory((prev) => {
    const next = prev === category ? null : category;

    // Fetch subcategories only once
    if (next && !subcategories[category]) {
      fetchSubcategories(category);
    }

    return next;
  });
}




  // =========================
  // NORMALIZE CATEGORY NAMES
  // =========================
  function normalizeCategory(cat) {
    if (!cat) return "Uncategorized";

    const map = {
      "MOOE": "MOOE",
      "20% BDF": "20% BDF",
      "5% BDRRMF": "5% BDRRMF",
      "15% GAD": "15% GAD",
      "Personal Services": "Personal Services",
      "1% Senior Citizen & PWD": "1% Senior & PWD",
    };

    return map[cat] || cat;
  }

  // =========================
  // BUILD CHART DATA (FIXED)
  // =========================
 const chartData = useMemo(() => {
  // 1ï¸âƒ£ Start STRICTLY from allocation totals (DB source of truth)
  const map = {};

  allocations.forEach((a) => {
    map[a.category] = {
      category: a.category,
      allocated: a.allocated,
      spent: 0, // default
    };
  });

  // 2ï¸âƒ£ Add expenses ONLY if category already exists
  transactions.forEach((t) => {
    const rawCategory =
      t.fund ||
      t.category_name ||
      t.category;

    const category = normalizeCategory(rawCategory);
    const spent = Number(t.withdrawal || 0);

    if (!spent) return;
    if (!map[category]) return; // â— DO NOT CREATE NEW CATEGORY

    map[category].spent += spent;
  });

  // 3ï¸âƒ£ Return allocations FIRST (sorted by allocated amount)
  return Object.values(map).sort(
    (a, b) => b.allocated - a.allocated
  );
}, [allocations, transactions]);

  // =========================
  // UI
  // =========================
return (
  <div className="space-y-6">
    {/* =========================
        HEADER
    ========================== */}
    <div className="rounded-xl bg-emerald-700 px-6 py-4 text-white shadow">
      <h1 className="text-xl font-semibold">
        ðŸ’¸ Expenses vs Budget Allocation ({selectedYear})
      </h1>
      <p className="text-xs opacity-90 mt-0.5">
        Official budget allocation with detailed breakdown
      </p>
    </div>

    {/* =========================
        BUDGET ALLOCATION
    ========================== */}
    <div className="bg-white p-5 rounded-xl border shadow-sm">
      <h3 className="font-semibold text-base mb-4 flex items-center gap-2">
        ðŸ“Š Budget Allocation
      </h3>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {allocations.map((a) => {
          const isOpen = expandedCategory === a.category;

          return (
            <div
              key={a.id}
              className={`rounded-lg border transition-all
                ${isOpen ? "border-emerald-400 shadow-sm" : "border-gray-200"}
              `}
            >
              {/* CARD HEADER */}
              <div className="px-4 py-3 flex justify-between items-start">
                <div>
                  <p className="text-[11px] text-black-500 uppercase tracking-wide">
                    {a.category}
                  </p>
                  <p className="text-xl font-semibold text-emerald-600 leading-tight">
                    â‚±{a.allocated.toLocaleString()}
                  </p>
                </div>

                <button
                  className="text-[11px] font-medium text-emerald-600 hover:underline"
                  onClick={() => toggleCategory(a.category)}
                >
                  {isOpen ? "Hide" : "Details"}
                </button>
              </div>

              {/* BREAKDOWN */}
              {isOpen && (
                <>
                  <div className="border-t" />
                  <div className="px-4 py-2 space-y-1 text-xs bg-gray-50 rounded-b-lg max-h-56 overflow-y-auto">
                    {subcategories[a.category]?.length ? (
                      subcategories[a.category].map((s) => (
                        <div
                          key={s.id}
                          className="flex justify-between items-center"
                        >
                          <span className="text-gray-700 truncate max-w-[70%]">
                            {s.description}
                          </span>
                          <span className="font-medium text-gray-900">
                            â‚±{Number(s.amount).toLocaleString()}
                          </span>
                        </div>
                      ))
                    ) : (
                      <p className="text-gray-400 italic">
                        No breakdown available
                      </p>
                    )}
                  </div>
                </>
              )}
            </div>
          );
        })}
      </div>
    </div>

  


    {/* =========================
        CHART
    ========================== */}
    <div className="bg-white p-6 rounded-2xl border shadow-sm">
      <h3 className="font-semibold text-lg mb-4">
        ðŸ“ˆ Allocation vs Expenses (by Category)
      </h3>

      <ResponsiveContainer width="100%" height={320}>
        <BarChart data={chartData}>
  <XAxis dataKey="category" />
  <YAxis />
  <Tooltip formatter={(v) => `â‚±${v.toLocaleString()}`} />
  <Legend />
  <Bar dataKey="allocated" fill="#10b981" name="Allocated" />
  <Bar dataKey="spent" fill="#ef4444" name="Spent" />
</BarChart>
      </ResponsiveContainer>
    </div>
  </div>
);
}